// /frontend/src/features/lineup/pages/TeamRosterPage.jsx
import React, { useEffect, useState } from 'react';
import Papa from 'papaparse';
import { useParams } from 'react-router-dom';

export default function TeamRosterPage() {
  // Get teamId from URL; default to 't001'
  const { teamId = 't001' } = useParams();
  const [lineup, setLineup] = useState([]);
  const [friendlyName, setFriendlyName] = useState('');

  // Load the team mapping from teamIDs.csv
  useEffect(() => {
    Papa.parse('/data/teamIDs.csv', {
      header: true,
      download: true,
      complete: (results) => {
        console.log('teamIDs CSV data:', results.data);
        // Find the row where the team_ids matches the URL teamId (case-insensitive)
        const mappingRow = results.data.find(
          (row) =>
            row.team_ids &&
            row.team_ids.trim().toLowerCase() === teamId.trim().toLowerCase()
        );
        if (mappingRow && mappingRow.teamName) {
          setFriendlyName(mappingRow.teamName);
        } else {
          setFriendlyName(teamId.toUpperCase());
        }
      },
      error: (err) => {
        console.error('Error parsing teamIDs CSV:', err);
        setFriendlyName(teamId.toUpperCase());
      }
    });
  }, [teamId]);

  // Load the team roster from master_lineup.csv
  useEffect(() => {
    Papa.parse('/data/master_lineup.csv', {
      header: true,
      download: true,
      complete: (results) => {
        console.log('master_lineup CSV data:', results.data);
        const data = results.data;
        const filtered = data.filter(
          (row) =>
            row.Team &&
            row.Team.trim().toLowerCase() === teamId.trim().toLowerCase()
        );
        console.log(`Filtered data for teamId "${teamId}":`, filtered);
        setLineup(filtered);
      },
      error: (err) => {
        console.error('Error parsing master_lineup CSV:', err);
      }
    });
  }, [teamId]);

  return (
    <div className="p-4">
      <h2 className="text-2xl font-bold mb-4">
        TEAM: {friendlyName || teamId.toUpperCase()}
      </h2>
      {lineup.length === 0 ? (
        <p>No players found for this team.</p>
      ) : (
        <ul>
          {lineup.map((player, idx) => (
            <li key={idx} className="border p-2 mb-1">
              <strong>{player['Nexus Being']}</strong>
              {player.Position && (
                <span className="ml-2">({player.Position})</span>
              )}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
