
import React, { useState, useEffect } from 'react';
import Papa from 'papaparse';

export default function TradePage() {
  const teamId = 't001'; // Temporarily hardcoded
  const otherTeamId = 'other'; // Demo placeholder

  const [myPlayers, setMyPlayers] = useState([]);
  const [otherTeamPlayers, setOtherTeamPlayers] = useState([]);
  const [selectedToTrade, setSelectedToTrade] = useState([]);
  const [selectedToReceive, setSelectedToReceive] = useState([]);

  useEffect(() => {
    Papa.parse('/data/master_lineup.csv', {
      header: true,
      download: true,
      complete: (result) => {
        const data = result.data;
        const myTeam = data.filter(
          p =>
            p.Team &&
            p.Team.trim().toLowerCase() === teamId.trim().toLowerCase()
        );
        const otherTeam = data.filter(
          p =>
            p.Team &&
            p.Team.trim().toLowerCase() === otherTeamId.trim().toLowerCase()
        );
        setMyPlayers(myTeam);
        setOtherTeamPlayers(otherTeam);
      }
    });
  }, [teamId, otherTeamId]);

  const toggleSelection = (player, selection, setSelection) => {
    const playerId = player['Nexus Being'];
    if (selection.includes(playerId)) {
      setSelection(selection.filter((id) => id !== playerId));
    } else {
      setSelection([...selection, playerId]);
    }
  };

  const proposeTrade = () => {
    alert(`Proposed Trade:
My Players to Trade: ${selectedToTrade.join(', ')}
Players to Receive: ${selectedToReceive.join(', ')}`);
    setSelectedToTrade([]);
    setSelectedToReceive([]);
  };

  return (
    <div className="p-4">
      <h2 className="text-2xl font-bold mb-4">Trade Players</h2>

      <div className="mb-6">
        <h3 className="text-xl mb-2">My Roster ({teamId.toUpperCase()})</h3>
        {myPlayers.length > 0 ? (
          <ul>
            {myPlayers.map((player, idx) => (
              <li
                key={idx}
                onClick={() => toggleSelection(player, selectedToTrade, setSelectedToTrade)}
                className={`flex justify-between border p-2 mb-1 cursor-pointer ${
                  selectedToTrade.includes(player['Nexus Being']) ? 'bg-blue-100' : ''
                }`}
              >
                <span>{player['Nexus Being']}</span>
              </li>
            ))}
          </ul>
        ) : (
          <p>No players in your roster.</p>
        )}
      </div>

      <div className="mb-6">
        <h3 className="text-xl mb-2">Other Team Roster ({otherTeamId.toUpperCase()})</h3>
        {otherTeamPlayers.length > 0 ? (
          <ul>
            {otherTeamPlayers.map((player, idx) => (
              <li
                key={idx}
                onClick={() => toggleSelection(player, selectedToReceive, setSelectedToReceive)}
                className={`flex justify-between border p-2 mb-1 cursor-pointer ${
                  selectedToReceive.includes(player['Nexus Being']) ? 'bg-green-100' : ''
                }`}
              >
                <span>{player['Nexus Being']}</span>
              </li>
            ))}
          </ul>
        ) : (
          <p>No players in the other team's roster.</p>
        )}
      </div>

      <button onClick={proposeTrade} className="bg-purple-500 text-white p-2">
        Propose Trade
      </button>
    </div>
  );
}
