import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import Papa from 'papaparse';

const positionMap = {
  "ROGUE-121": "Field Leader",
  "DOMINO-047": "Ranger",
  "MAGIK-090": "Vanguard",
  "COLOSSUS-032": "Enforcer",
  "GAMBIT-057": "Ghost Operative",
  "DOCTOR-STRANGE-046": "Psi Operative",
  "EMMA-FROST-052": "Psi Operative",
  "PHOENIX-001": "Sovereign",
  "Rogue": "Field Leader",
  "Domino": "Ranger",
  "Magik": "Vanguard",
  "Colossus": "Enforcer",
  "Gambit": "Ghost Operative",
  "Doctor Strange": "Psi Operative",
  "Emma Frost": "Psi Operative",
  "Jean Grey (Phoenix)": "Sovereign"
};

export default function DirectorDashboardPage() {
  const { teamId } = useParams();
  const clearedTeam = sessionStorage.getItem('clearedTeam');
  const [aar, setAAR] = useState(null);
  const [messages, setMessages] = useState([]);
  const [record, setRecord] = useState({ success: 0, failure: 0, lvs: null, lls: null });
  const [roster, setRoster] = useState([]);

  const teamNameMap = {
    T001: "Gambit Is A Pretty Pretty Princess",
    T002: "Costco Wholesale",
    T003: "Sterling's Silly Soldiers",
    T004: "Dumb and Dumber Returns",
    T005: "Ligmasigma II"
  };

  const displayName = teamNameMap[teamId] || teamId;
  const isMC = teamId === "T001";

  useEffect(() => {
    fetch(`/data/aar/${teamId}.json`).then(res => res.json()).then(setAAR).catch(() => {});
    fetch(`/data/messages/${teamId}.json`).then(res => res.json()).then(setMessages).catch(() => {});
    setRecord({ success: 0, failure: 0, lvs: null, lls: null });

    const loadCSV = (url) => new Promise((resolve) => {
      Papa.parse(url, {
        header: true,
        download: true,
        complete: (results) => resolve(results.data)
      });
    });

    const loadRoster = async () => {
      const [lineup, characters] = await Promise.all([
        loadCSV('/meta-league/data/master_lineup.csv'),
        loadCSV('/meta-league/data/all_characters.csv')
      ]);

      const photoMap = {};
      characters.forEach((c) => {
        const name = c['Nexus Being'];
        const photo = c['Photo'];
        if (name && photo && photo.startsWith("http")) {
          photoMap[name] = photo;
        }
      });

      const filtered = lineup
        .filter(row => row.Team === teamId)
        .slice(0, 8);

      const merged = filtered.map(entry => ({
        name: entry['Nexus Being'],
        rank: entry['Rank'],
        position: entry['Position'],
        photo: photoMap[entry['Nexus Being']] || 'https://via.placeholder.com/120?text=No+Image',
      }));

      setRoster(merged);
    };

    loadRoster();
  }, [teamId]);

  if (clearedTeam !== teamId) {
    return (
      <div className="min-h-screen bg-[#0D1B2A] text-white font-sans p-6">
        <p className="text-red-600 font-bold">
          Access Denied. Return to <a href="/access" className="underline text-[#0D1B2A]">Access Clearance</a>.
        </p>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-[#0D1B2A] text-white font-sans p-6">
      <div className="grid grid-cols-2 gap-6">
        <div>
          <h1 className="text-4xl font-mono text-[#E63946] mb-4">{displayName} Dashboard</h1>
          <p className="mb-6 text-md italic">
            Mission Record:  {record.success} {record.failure}LVS: {record.lvs ?? "--"}LLS: {record.lls ?? "--"}
          </p>

          <section className="mb-6">
            <h2 className="text-xl font-bold mb-2"> After Action Report</h2>
            {aar ? (
              <pre className="bg-white p-4 rounded overflow-auto">{JSON.stringify(aar, null, 2)}</pre>
            ) : (
              <>
                <p>No AAR available yet.</p>
                <p><button className="underline text-blue-400 hover:text-blue-200 mt-1" onClick={() => alert('Archived AARs coming soon.')}>View Archived AARs</button></p>
              </>
            )}
          </section>

          <section className="mb-6">
            <h2 className="text-xl font-bold mb-2"> Advisory Messages</h2>
            {messages.length > 0 ? (
              <ul className="list-disc ml-5">{messages.map((msg, i) => <li key={i}>{msg}</li>)}</ul>
            ) : (
              <>
                <p>No messages at this time.</p>
                <p><button className="underline text-blue-400 hover:text-blue-200 mt-1" onClick={() => alert('Advisory Log coming soon.')}>View Advisory Log</button></p>
              </>
            )}
          </section>

          {isMC && (
            <section>
              <h2 className="text-xl font-bold mb-2"> Master Commander Panel</h2>
              <p className="mb-2">You have mission assignment authority this week. Choose your deployment strategy:</p>
              <button className="bg-[#E63946] text-white px-4 py-2 rounded hover:bg-red-700">Assign Missions</button>
            </section>
          )}
        </div>

        <div className="bg-[#E63946] text-white shadow-lg p-4 rounded-xl border-2 border-black">
          <h2 className="text-xl font-semibold text-[#0D1B2A] mb-4 text-center">Field Team Manifest</h2>
          <ul className="grid grid-cols-3 gap-6 mb-6">
            {roster.slice(0, 3).map((char, idx) => (
              <li key={`row1-${idx}`} className="text-center">
                <div className="w-28 h-28 mx-auto mb-2 rounded-full border-4 border-white overflow-hidden bg-white">
                  <img src={char.photo} alt={char.name} className="object-cover w-full h-full" />
                </div>
                <div className="text-base font-bold uppercase text-[#0D1B2A]">{char.name}</div>
                <div className="text-sm text-white mt-1">{positionMap[char.name] || "Unassigned Role"}</div>
              </li>
            ))}
          </ul>
          <ul className="flex justify-center gap-6 mb-6">
            {roster.slice(6, 8).map((char, idx) => (
              <li key={`row3-${idx}`} className="text-center">
                <div className="w-28 h-28 mx-auto mb-2 rounded-full border-4 border-white overflow-hidden bg-white">
                  <img src={char.photo} alt={char.name} className="object-cover w-full h-full" />
                </div>
                <div className="text-base font-bold uppercase text-[#0D1B2A]">{char.name}</div>
                <div className="text-sm text-white mt-1">{positionMap[char.name] || "Unassigned Role"}</div>
              </li>
            ))}
          </ul>
          <ul className="grid grid-cols-3 gap-6">
            {roster.slice(3, 6).map((char, idx) => (
              <li key={`row2-${idx}`} className="text-center">
                <div className="w-28 h-28 mx-auto mb-2 rounded-full border-4 border-white overflow-hidden bg-white">
                  <img src={char.photo} alt={char.name} className="object-cover w-full h-full" />
                </div>
                <div className="text-base font-bold uppercase text-[#0D1B2A]">{char.name}</div>
                <div className="text-sm text-white mt-1">{positionMap[char.name] || "Unassigned Role"}</div>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
}
