import React, { useEffect, useState } from 'react';
import { Tooltip } from 'react-tooltip';
import Papa from "papaparse";

export default function DataTablePage() {
  const [data, setData] = useState([]);
  const [legendMap, setlegendMap] = useState({});
  const [filterText, setFilterText] = useState('');
  const [sortColumn, setSortColumn] = useState('');
  const [sortOrder, setSortOrder] = useState('asc');
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(10);

  useEffect(() => {
    fetch('/meta-league/data/legendMap.json')
      .then(res => res.json())
      .then(setlegendMap)
      .catch(err => console.error("Error loading legendMap:", err));
  }, []);

  useEffect(() => {
    Papa.parse('/meta-league/data/all_characters.csv', {
      header: true,
      download: true,
      complete: (results) => {
        console.log("DataTablePage CSV data:", results.data);
        setData(results.data);
      },
      error: (err) => {
        console.error("Error parsing CSV in DataTablePage:", err);
      }
    });
  }, []);

  const filteredData = data.filter(row => {
    const searchString = `${row['Nexus Being'] || ''} ${row.Team || ''} ${row.Position || ''} ${row['Primary Type'] || ''}`.toLowerCase();
    return searchString.includes(filterText.toLowerCase());
  });

  const sortedData = [...filteredData].sort((a, b) => {
    if (!sortColumn) return 0;
    const aVal = a[sortColumn] || '';
    const bVal = b[sortColumn] || '';
    return sortOrder === 'asc'
      ? aVal.toString().localeCompare(bVal.toString())
      : bVal.toString().localeCompare(aVal.toString());
  });

  const totalPages = Math.ceil(sortedData.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const paginatedData = sortedData.slice(startIndex, startIndex + itemsPerPage);

  const handleSort = (column) => {
    if (sortColumn === column) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortColumn(column);
      setSortOrder('asc');
    }
  };

  const headers = data.length > 0 ? Object.keys(data[0]) : [];

  const renderCellValue = (header, value) => {
    if (
      header.toLowerCase() === 'photo' &&
      typeof value === 'string' &&
      value.startsWith("http")
    ) {
      return (
        <img
          src={value}
          alt="Character"
          className="w-12 h-12 object-cover rounded-full border"
        />
      );
    }
    return typeof value === 'object' ? JSON.stringify(value) : value;
  };

  return (
    <div className="p-4 font-sans" style={{ fontFamily: "Helvetica, Arial, sans-serif" }}>
      <h2 className="text-4xl mb-4 font-marvel text-heroRed text-center">
        Free Agency
      </h2>

      <div className="mb-4">
        <input
          type="text"
          placeholder="Search by name, team, position, type, etc..."
          value={filterText}
          onChange={e => {
            setFilterText(e.target.value);
            setCurrentPage(1);
          }}
          className="border p-2 w-full"
        />

        <div className="mb-4 flex items-center gap-2">
          <label htmlFor="itemsPerPage">Rows per page:</label>
          <select
            id="itemsPerPage"
            value={itemsPerPage}
            onChange={e => {
              setCurrentPage(1);
              setItemsPerPage(parseInt(e.target.value));
            }}
            className="border p-1"
          >
            {[5, 10, 20, 50].map(num => <option key={num} value={num}>{num}</option>)}
          </select>
        </div>
      </div>

      {/* Tooltip anchors */}
      {["INT", "STR", "SPD", "DUR", "EP", "FS", "LDR", "RES", "WIL", "LCK", "ESP", "OP", "AM", "SBY"].map(stat => (
        <Tooltip key={stat} anchorId={`tooltip-${stat}`} content={legendMap[stat]?.description || stat} />
      ))}

      {data.length === 0 ? (
        <p>Loading data...</p>
      ) : (
        <>
          <table className="min-w-full border-collapse border border-gray-300">
            <thead>
              <tr>
                {headers.map((header, idx) => (
                  <th
                    key={idx}
                    id={`tooltip-${header}`}
                    className="border border-gray-300 px-2 py-0.5 text-sm bg-gray-200 cursor-pointer"
                    onClick={() => handleSort(header)}
                  >
                    {header}
                    {sortColumn === header && (sortOrder === 'asc' ? ' ' : ' ')}
                  </th>
                ))}
              </tr>
            </thead>
            <tbody>
              {paginatedData.map((row, rowIndex) => (
                <tr key={rowIndex} className={rowIndex % 2 === 0 ? 'bg-white' : 'bg-gray-100'}>
                  {headers.map((header, idx2) => (
                    <td key={idx2} className="border border-gray-300 px-2 py-0.5 text-sm">
                      {renderCellValue(header, row[header])}
                    </td>
                  ))}
                </tr>
              ))}
            </tbody>
          </table>

          {/* Pagination */}
          <div className="mt-4 flex items-center justify-center space-x-4">
            <button
              onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
              disabled={currentPage === 1}
              className="px-3 py-[2px] border rounded disabled:opacity-50"
            >
              Prev
            </button>
            <span>
              Page {currentPage} of {totalPages}
            </span>
            <button
              onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
              disabled={currentPage === totalPages}
              className="px-3 py-[2px] border rounded disabled:opacity-50"
            >
              Next
            </button>
          </div>
        </>
      )}
    </div>
  );
}
