import React, { useState, useEffect } from 'react';
import Papa from 'papaparse';
import { useParams } from 'react-router-dom';

export default function AddDropPage() {
  const { teamId = 't001' } = useParams();
  const [freeAgents, setFreeAgents] = useState([]);
  const [teamPlayers, setTeamPlayers] = useState([]);

  useEffect(() => {
    Papa.parse('/data/master_lineup.csv', {
      header: true,
      download: true,
      complete: (result) => {
        console.log("Add/Drop CSV Data:", result.data);
        const data = result.data;
        const team = data.filter(
          p => p.Team && p.Team.trim().toLowerCase() === teamId.trim().toLowerCase()
        );
        const free = data.filter(
          p => !p.Team || p.Team.trim() === ''
        );
        setTeamPlayers(team);
        setFreeAgents(free);
      },
      error: (err) => {
        console.error("Error parsing CSV in Add/Drop:", err);
      }
    });
  }, [teamId]);

  const addPlayer = (player) => {
    setFreeAgents(prev => prev.filter(p => p['Nexus Being'] !== player['Nexus Being']));
    setTeamPlayers(prev => [...prev, { ...player, Team: teamId }]);
    alert(`Added ${player['Nexus Being']} (stub)!`);
  };

  const dropPlayer = (player) => {
    setTeamPlayers(prev => prev.filter(p => p['Nexus Being'] !== player['Nexus Being']));
    setFreeAgents(prev => [...prev, { ...player, Team: '' }]);
    alert(`Dropped ${player['Nexus Being']} (stub)!`);
  };

  return (
    <div className="p-4">
      <h2 className="text-2xl font-bold mb-4">Add/Drop Players for {teamId.toUpperCase()}</h2>
      <div className="mb-6">
        <h3 className="text-xl mb-2">Team Roster</h3>
        {teamPlayers.length > 0 ? (
          <ul>
            {teamPlayers.map((player, idx) => (
              <li key={idx} className="flex justify-between border p-2 mb-1">
                <span>{player['Nexus Being']}</span>
                <button
                  onClick={() => dropPlayer(player)}
                  className="bg-red-500 text-white px-2"
                >
                  Drop
                </button>
              </li>
            ))}
          </ul>
        ) : (
          <p>No players on the team.</p>
        )}
      </div>
      <div>
        <h3 className="text-xl mb-2">Free Agents</h3>
        {freeAgents.length > 0 ? (
          <ul>
            {freeAgents.map((player, idx) => (
              <li key={idx} className="flex justify-between border p-2 mb-1">
                <span>{player['Nexus Being']}</span>
                <button
                  onClick={() => addPlayer(player)}
                  className="bg-green-500 text-white px-2"
                >
                  Add
                </button>
              </li>
            ))}
          </ul>
        ) : (
          <p>No free agents available.</p>
        )}
      </div>
    </div>
  );
}
