// /frontend/src/features/data/DriverLicenseList.jsx
import React, { useEffect, useState } from 'react';
import Papa from 'papaparse';
import DriverLicenseCard from './DriverLicenseCard';

// Helper function to load a CSV file using Papa Parse
function loadCSV(url) {
  return new Promise((resolve, reject) => {
    Papa.parse(url, {
      header: true,
      download: true,
      complete: (results) => resolve(results.data),
      error: (err) => reject(err)
    });
  });
}

export default function DriverLicenseList() {
  const [mergedData, setMergedData] = useState([]);

  useEffect(() => {
    Promise.all([
      loadCSV('/data/ToolTip.txt'),        // Contains descriptive fields
      loadCSV('/data/all_characters.csv')    // Contains Photo field
    ])
      .then(([tooltipData, allCharactersData]) => {
        console.log("Tooltip Data:", tooltipData);
        console.log("All Characters Data:", allCharactersData);
        // Merge data: For each row in tooltipData, find a matching row in all_characters.csv by "Nexus Being"
        const merged = tooltipData.map(row => {
          const mergedRow = { ...row };
          const matching = allCharactersData.find(
            item => item['Nexus Being'] === row['Nexus Being']
          );
          if (matching && matching['Photo']) {
            mergedRow.Photo = matching['Photo'];
          }
          return mergedRow;
        });
        setMergedData(merged);
      })
      .catch(err => {
        console.error("Error loading CSV files:", err);
      });
  }, []);

  return (
    <div className="p-4 space-y-4">
      <h2 className="text-2xl font-bold mb-4">Driver License Cards</h2>
      {mergedData.length === 0 ? (
        <p>Loading data...</p>
      ) : (
        mergedData.map((row, idx) => (
          <div key={idx}>
            <DriverLicenseCard row={row} />
          </div>
        ))
      )}
    </div>
  );
}
