import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function DirectorAccessPage() {
  const [input, setInput] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate();

const phraseMap = {
  "team001": "T001",
  "team002": "T002",
  "team003": "T003",
  "team004": "T004",
  "team005": "T005",
  "team006": "T006",
  "team007": "T007",
  "team008": "T008",
  "team009": "T009",
  "team010": "T010",
};

  const handleSubmit = (e) => {
    e.preventDefault();
    const normalized = input.trim().toLowerCase();
    if (phraseMap[normalized]) {
      sessionStorage.setItem("clearedTeam", phraseMap[normalized]);
      navigate(`/director-dashboard/${phraseMap[normalized]}`);
    } else {
      setError("Access phrase not recognized.");
    }
  };

  return (
    <div className="min-h-screen bg-heroWhite text-heroBlack font-sans flex items-center justify-center p-4">
      <div className="max-w-md w-full bg-white p-6 rounded shadow-md border">
        <h2 className="text-2xl font-marvel text-center text-heroBlue mb-4">Director Access Clearance</h2>
        <form onSubmit={handleSubmit}>
          <input
            type="text"
            placeholder="Enter your team's access phrase"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            className="w-full p-2 border border-gray-300 rounded mb-4"
          />
          <button
            type="submit"
            className="w-full bg-heroRed text-white py-2 px-4 rounded hover:bg-red-700"
          >
            Enter
          </button>
          {error && <p className="text-red-600 mt-2">{error}</p>}
        </form>
      </div>
    </div>
  );
}