
import React from 'react';
import { HashRouter as Router, Routes, Route, Link } from 'react-router-dom';

import HomePage from './features/home/pages/HomePage';
import TeamRosterPage from './features/lineup/pages/TeamRosterPage';
import DataTablePage from './features/data/DataTablePage';
import AddDropPage from './features/director/pages/AddDropPage';
import TradePage from './features/director/pages/TradePage';
import LoginPage from './features/auth/LoginPage';
import ProtectedRoute from './features/auth/ProtectedRoute';
import { AuthProvider } from './features/auth/AuthContext';
import DriverLicenseList from './features/data/DriverLicenseList';
import SimulationStatsPage from './features/simulation/SimulationStatsPage';
import DirectorAccessPage from './features/director/pages/DirectorAccessPage';
import DirectorDashboardPage from './features/director/pages/DirectorDashboardPage';

function App() {
  return (
    <AuthProvider>
      <Router>
        <div className="p-4">
          <nav className="mb-6 flex justify-evenly items-center font-mono font-bold text-white bg-black p-3 rounded">
            <Link to="/" className="hover:text-yellow-400">Home</Link>
            <Link to="/data-table" className="hover:text-yellow-400">Nexus Beings</Link>
            <Link to="/simulation-stats" className="hover:text-yellow-400">Nexus Metrics</Link>
            <Link to="/access" className="hover:text-yellow-400">Director Portal</Link>
            <Link to="/director/add-drop" className="hover:text-yellow-400">Add/Drop</Link>
          </nav>

          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/team-roster/:teamId" element={<TeamRosterPage />} />
            <Route path="/data-table" element={<DataTablePage />} />
            <Route path="/simulation-stats" element={<SimulationStatsPage />} />
            <Route path="/access" element={<DirectorAccessPage />} />
            <Route path="/director-dashboard/:teamId" element={<DirectorDashboardPage />} />
            <Route path="/driver-license" element={<DriverLicenseList />} />
            <Route path="/login" element={<LoginPage />} />
            <Route path="/director/add-drop" element={<ProtectedRoute><AddDropPage /></ProtectedRoute>} />
            <Route path="/director/trade" element={<ProtectedRoute><TradePage /></ProtectedRoute>} />
          </Routes>
        </div>
      </Router>
    </AuthProvider>
  );
}

export default App;
