// /frontend/src/features/data/DriverLicenseCard.jsx
import React from 'react';

export default function DriverLicenseCard({ row }) {
  // Use the CSV headers exactly:
  const photoUrl = row['Photo'];
  const name = row['Nexus Being'] || 'Unknown';
  const description = row['Description'] || 'No description available';
  const primary = row['Primary Type'] || 'N/A';
  const secondary = row['Secondary Type'] || 'N/A';
  
  return (
    <div className="flex border border-gray-300 p-4 rounded-md shadow-md max-w-md">
      {/* Photo Section */}
      <div className="w-1/3">
        {photoUrl && typeof photoUrl === 'string' && photoUrl.match(/\.(jpeg|jpg|gif|png)$/i) ? (
          <img
            src={photoUrl}
            alt="Character"
            className="w-full h-32 object-cover rounded-md"
          />
        ) : (
          <div className="w-full h-32 bg-gray-200 flex items-center justify-center text-sm">
            No Image
          </div>
        )}
      </div>
      {/* Details Section */}
      <div className="w-2/3 pl-4">
        <h3 className="text-xl font-bold">{name}</h3>
        <p><strong>Description:</strong> {description}</p>
        <p><strong>Primary Type:</strong> {primary}</p>
        <p><strong>Secondary Type:</strong> {secondary}</p>
      </div>
    </div>
  );
}
